*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Roboto';
}
.main{
  padding: 0 8px;
  margin: 0 auto;
}
.center{
  width: 90%;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
}
header{
  background: #032541;
}
header .inner-content{
  display: flex;
  justify-content: space-between;
  align-items: center;

  padding: 25px 0;
  margin: auto 30px;
}
header .inner-content li{
  display: inline-block;
  margin: 0 10px;
}
header a{
  color: #fff;
  text-decoration: none;
  font-size: 1.2em;
  font-family: 'Roboto', sans-serif;
  font-weight: 400;

  transition: all 0.3s ease;
}

.logo span{
  color: crimson;
  letter-spacing: 2px;
}
header a:hover{
  color: crimson;
}
.search-bars{
  padding: 30px 0;
  max-width: 500px;
  margin: 0 auto;
}
.search-bars .input-wrappers{
  width: 100%;
  padding: 10px 15px;

  box-shadow: 0 4px 4px rgba(0,0,0,0.4);
  border-radius: 10px;

  font-size: 1em;
  letter-spacing: 1px;
  font-family: 'Roboto', sans-serif;
  font-weight: 100;


  color: #fff;
  background: #032541;
  

  outline: none;
  border: none;
}
.search-bar .input-wrapper:focus{
  border: 2px solid #48dbfb;
}
.heading{
  margin: 0 auto;
  text-align: center;
}
.results{
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin: 40px auto;
  overflow: hidden;
}
.results .card1{
  position: relative;
  height: auto;
  width: 250px;
  background: #ffff;
  margin: 20px auto;
  background-size: cover;
  border-radius: 20px;
  transition: 0.3s ease-in-out;
  padding-bottom: 10px;
  cursor: pointer;
  box-shadow: 0 4px 5px rgba(0,0,0,0.4);

}
.title{
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 5px 20px;
  margin: auto 0;
}
.title h6{
  font-size: 1em;
  color: #000;
  font-weight: 400;
  font-family: 'Roboto', sans-serif;
}
.results .card1 img{
  display: block;
  width: 100%;
  border-radius: 20px;
}

.searchBtn{
  display: flex;
  justify-content: center;
}

.card2{
  width:800px;
  height:430px;
  background:transparent;
  position:absolute;
  left:0;
  right:0;
  margin: auto;
  top:40px;
  bottom:0;
  box-shadow: 0px 20px 30px 3px rgba(0, 0, 0, 0.55);
}
.card2_left{
    width:40%;
    height:480px;
    float:left;
    overflow:hidden;
    background:transparent;
}
.card2_left img{
  width: 100%;
  height: auto;
  position: relative;
}
.card2_right{
    width:60%;
    float:left;
    background:#000000;
    height: 480px;
    padding: 20px;
}
.card2_right p{
  color: #fff;
  font-size: 2em;
  font-weight: 400;
  font-family: 'Roboto', sans-serif;
}
.card2_right .list p:nth-child(1){
  color: #fff;
  font-size: 1em;
  font-weight: 300;
  font-family: 'Roboto', sans-serif;
}
.card2_right .list p:nth-child(2){
  color: #fff;
  font-size: 1.2em;
  font-weight: 400;
  font-family: 'Roboto', sans-serif;
}
.card2_right .overview{
  padding: 20px 0;
  color: beige;
  font-size: 1em;
  font-weight: 300;
  font-family: 'Roboto', sans-serif;
}
.card2_right .list{
  display: flex;
  justify-content: space-between;
  align-items: center;
}
@media (max-width:767px){
  header .inner-content{
  
    padding: 5px 0;
    margin: auto 10px;
  }
  header .inner-content li{
    display: inline-block;
    margin: 0 5px;
  }
  header a{
    font-size: .9em;
  }
  .results{
    margin: 0 auto;
    display: flex;
    justify-content: center;
    overflow: hidden;
  }
  .results .card1{
    position: relative;
    text-align: center;
    width: 25%;
    height: auto;
    min-width: 200px;
    padding: 0;
 
  }
  .card_responsive{
    margin: 0 auto;
    width: 300px;
    height: auto;
    position: relative;
    
  }
  .card_responsive .cad{
    width: 100%;
    height: auto;
  }

}